import PropTypes from "prop-types"
import React from "react"
import AniLink from "gatsby-plugin-transition-link/AniLink"

const Btn = ({ btntext, btnlink, paddingLeft, marginLeft }) => (


    <div style={{ width: `200px`, height: `auto`, cursor: `pointer`, marginBottom: '10px' }} >

        <AniLink fade to={`${btnlink}`} duration={.5}
            css={{
                fontFamily: 'Open Sans',
                fontWeight: '700',
                fontSize: '20px',
                color: 'black',
                transition: 'all .2s',
                background: 'transparent',
                webKitTransition: 'all .2s',

                textTransform: 'uppercase',
                WebkitTextStroke: '1px #fff',
                paddingLeft: paddingLeft,
                marginLeft: '27%',
                width: '300px',
                height: 'auto',
                ':hover': {
                    color: '#fff',
                    WebkitTextStroke: '1px #000',

                },

                '@media(min-width:700px)': {
                    fontSize: '45px',
                    marginLeft: marginLeft
                }
            }}
            activeStyle={{ color: '#fff', WebkitTextStroke: '1px #000', background: 'transparent' }}
        >

            {btntext}



        </AniLink>

    </div>
)



Btn.propTypes = {
    btntext: PropTypes.string,
}

Btn.defaultProps = {
    paddingLeft: `0px`,
    marginLeft: `0px`
}

export default Btn